@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
body{
    font-family: 'Poppins', sans-serif !important;
    /* background-color:#1E1E20 !important; */
    /* background-color: #06090f !important; */
    /* background-color: #000 !important; */
    background: #fff !important;
    text-align: center;
    height:100%;
}
img{
  height:auto;
  max-width:100%;
}
a:hover{
text-decoration: none !important;
}
h1, h2, h3, h4, h5, h6, p{
  margin:0;
}
.banner{
    display:-webkit-flex;
    display:flex;
    height:calc(100vh - 70px);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.banner .banner-heading{
    font-size: 70px;
    color:#BBBBBB;
    text-align: center;
    display:inline-block;
    font-weight: 500;
    margin-top:-40px;
}
.banner .banner-heading-span{
    font-size:70px;
    color:#222222;
    display:inline-block;   
}
.nice-to-meet{
    height:100vh;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    background-repeat: no-repeat;
    width:100%;
    background-position: center;
    border-radius: 10px;
    background-size: cover;
    padding:15vh;
  }
  .nice-to-meet-text{
    margin-top:10vh;
    font-size: 69px;
    font-weight: 500;
    color:#403D3D;
    margin-bottom:40vh;
  }
  .nice-to-meet-about{
    font-size: 30px;
    font-weight:400;
    color:#403D3D;
  }
.design-info{
    height:100vh;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-repeat: no-repeat;
    width:100%;
    background-position: center;
    border-radius: 10px;
    background-size: cover;
}
.design-text{
    color:#BBBBBB;
    font-size:40px;
    font-weight: 400;
    width:60%;
    text-align: center;
    margin:0 !important;
}
.design-text-span{
    color:#222222;   
    font-size:40px;
}
.portfolio-home{
    text-align: center;
    cursor: pointer;
    margin-bottom:50px;
    -webkit-transition: all 400ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 400ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 400ms ease-in;
    -moz-transform: scale(1);
    transition: all 400ms ease-in;
    transform: scale(1);
}
.portfolio-home-image{
    width:100%;
    border-radius: 10px;
    height:auto;
}.portfolio-home:hover{
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(0.95);
    -ms-transition: all 400ms ease-in;
    -ms-transform: scale(0.95);   
    -moz-transition: all 400ms ease-in;
    -moz-transform: scale(0.95);
    transition: all 400ms ease-in;
    transform: scale(0.95);
}
@media(max-width:420px){
    .banner .banner-heading{
        font-size:40px;
    }
    .banner .banner-heading-span{
        font-size:40px;
    }
    .nice-to-meet{
        padding:20px;
        height:auto;
    }
    .nice-to-meet-text{
        margin-top: 5vh;
font-size: 39px;
font-weight: 500;
color: #403D3D;
margin-bottom: 10vh;
    }
    .design-info{
        height:auto;
        padding:10px;
        margin:40px 0px;
    }
    .design-text{
        font-size:20px;
        width:100%;
    }
    .design-text-span{
        font-size:20px;
    }
    .portfolio-home{
        margin-bottom:20px;
    }
    
}
.footer-box{
    background:#F5F5F5;
    display: -webkit-flex;
    display: flex;
    height:40vh;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding:10px;
    text-align: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.footer-box .connect{
    color:#222222;
    font-size:85px;
    display:inline-block;
    text-align: center;
    margin-bottom:50px;
}
.footer-box .connect-span{
    color:#BBBBBB;
    display:inline-block;
}
.footer-box .footer-links{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width:90%;
}
.footer-box .footer-links .link{
    font-size:20px;
    color:#BBBBBB;
    text-align: center;
    font-weight: 300;
    transition:0.3s;
    cursor: pointer;
}
.footer-box .footer-links .link:hover{
    color:#8a8a8a;
}
@media(max-width:420px){
    .footer-box{
        height:30vh;
    }
    .footer-box .connect{
        font-size:40px;
    }
    .footer-box .footer-links{
        width:100%;
    }
    .footer-box .footer-links .link{
        font-size:14px;
        text-align: center;
    }
}

.toggle-button{
background-color:transparent;
display:-webkit-flex;
display:flex;
-webkit-flex-direction: column;
        flex-direction: column;
border:none;
cursor: pointer;
}
.toggle-button:focus{
outline:none;
}
.toolbar{
    position:-webkit-sticky;
    position:sticky;
    width:100%;
    height:70px;
    top:0px;
    left:0px;
    z-index:899;
  }
  .toolbar .toolbar-navigation{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    height:100%;
    padding:10px 25px;
  }
  .toolbar .toolbar-navigation-items ul{
    list-style: none;
    margin:0;
    padding:0;
    display:-webkit-flex;
    display:flex;
  }
  .toolbar .toolbar-navigation-items li{
    padding: 5px 15px;
  }
  .toolbar .toolbar-logo{
    margin-left:10px;
  }
  .toolbar .toolbar-logo a{
    text-decoration: none;
  }
  .toolbar .toolbar-navigation-items a{
    text-decoration: none;
    color:#403D3D;
    font-size:16px;
    font-weight: 500;
    transition: 0.3s;
  }
  .toolbar .toolbar-navigation-items a:hover,
  .toolbar .toolbar-navigation-items a:active{
    border-bottom:3px solid #00D1A1;
  }

  .spacer{
    -webkit-flex-grow:1;
            flex-grow:1;
  }
  @media(max-width:760px){
    .toolbar-navigation-items{
      display:none;
    }
  }
  @media(min-width:761px){
    .toggle-button{
      display:none;
    }
  }
  @media(max-width:420px){
    .toolbar-logo{
      width:25px;
      height:auto;
    }
    .toggle-button img{
      width:25px;
      height:auto;
    }
  }
.side-drawer{
height:100%;
background-color: #05080d;
position: fixed;
top:0;
left:0;
width:70%;
max-width:400px;
z-index:1000;
-webkit-transform: translateX(-100%);
        transform: translateX(-100%);
transition: -webkit-transform 0.3s ease-out;
transition: transform 0.3s ease-out;
transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.side-drawer.open{
-webkit-transform: translateX(0px);
        transform: translateX(0px);
box-shadow: 2px 0px 20px rgba(0,0,0,0.43);
}
.side-drawer ul{
display:-webkit-flex;
display:flex;
-webkit-flex-direction: column;
        flex-direction: column;
list-style: none;
-webkit-justify-content: center;
        justify-content: center;
height: 100%;
}
.side-drawer li{
margin:10px 0px;
}
.side-drawer a{
color:#d9d9d9;
text-decoration: none;
}
.side-drawer a:hover,
.side-drawer a:focus{
color:#9c9c9c;
}

.backdrop{
    position:fixed;
    width:100%;
    height:100%;
    background: rgba(0,0,0,0.3);
    z-index:999;
    top:0;
    left:0;
  }
